.home {
  padding-bottom: 500px; }
  .home h1 {
    text-align: center; }
  .home section {
    padding: 100px; }
  .home .about {
    min-height: 500px; }
  .home .card {
    max-width: 400px;
    max-height: 600px; }
    .home .card:hover {
      opacity: 0.6;
      letter-spacing: 1px;
      -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
      box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
      -webkit-transition: all 0.4s ease 0s;
      -o-transition: all 0.4s ease 0s;
      transition: all 0.4s ease 0s; }
  .home .media {
    min-height: 250px;
    padding-top: 56%; }



